import React from "react"
import { GatsbyImage, getImage } from "gatsby-plugin-image"
import { graphql, useStaticQuery } from "gatsby"

const ClientSection = () => {
  const data = useStaticQuery(graphql`
    query {
      img10: allFile(filter: { name: { in: ["11", "10"] } }) {
        edges {
          node {
            childImageSharp {
              gatsbyImageData(
                placeholder: BLURRED
                formats: [AUTO, WEBP, AVIF]
                width: 220
                height: 110
              )
            }
          }
        }
      }
    }
  `)

  return (
    <>
      <section className="clients-section" style={{ height: "234px" }}>
        <div className="auto-container">
          <div className="sponsors-outer">
            {/* <!-- Sponsors Carousel --> */}

            <ul className="sponsors-carousel owl-carousel owl-theme owl-rtl owl-loaded owl-drag">
              {data.img10.edges.map(item => {
                return (
                  <li
                    className="slide-item"
                    style={{ width: "234px", float: "left" }}
                  >
                    <figure className="image-box">
                      {/* <a href="#"> */}
                      <GatsbyImage
                        image={getImage(item.node.childImageSharp)}
                        alt=""
                        width={220}
                        height={110}
                      />
                      {/* </a> */}
                    </figure>
                  </li>
                )
              })}
            </ul>
          </div>
        </div>
      </section>
    </>
  )
}

export default ClientSection
